html, 
body {
  font-family: 'Lato', arial, sans-serif;
  color: #434343;
  background: var(--background);
  font-size: 16px;
  line-height: 1.42854458;
}
h1,
h2,
h3,
h4,
h5,
h6, 
.mt-0 {
	font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  color: #49515a;
}
.container-fluid {
	font-size: 14px;
  background: var(--background);
}
.container {
  background: var(--background);
}
.header {
	border-top: 10px solid #778492;
	padding: 30px 0;
	background: var(--background_header);
}
.mb-0 {
	font-size: 22px;
  color: #778492;
  font-weight: 400;
}
.profile {
  background: var(--background_header);
}
.header .social a {
  background: #b0b7bf;
  width: 36px;
  height: 36px;
  display: inline-block;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius: 50%;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  color: #fff;
  text-align: center;
}
.header .social a:hover {
  background: #778492;
}
.header .social a .fa {
  font-size: 20px;
  padding-top: 8px;
}
.list-inline li {
	display: inline-block;
	padding: 10px 5px 0 0;
}
.content {
  padding: 20px 20px 20px 0;
}
.info .fa {
  margin-right: 15px;
  color: #ccd1d6;
}
.info .fa.fa-envelope-o {
  font-size: 14px;
}
.info ul {
  margin-bottom: 0;
}
.info li {
  margin-bottom: 15px;
}
.info li:last-child {
  margin-bottom: 0;
}
.aside {
	display: block;
  padding-top: 30px;
}
.section {
  margin-bottom: 15px;
  display: block;
  margin-left: -20px;
  margin-right: 15px;
}
.section .section-inner {
  padding: 20px;
  background-color: #fff;
}
.list-unstyled li {
  color: #007bff;
}
.title h3 {
  font-size: 14px;
  color: #778492;
}
.title {
  font-size: 20px;
  color: #778492;
  font-weight: 600;
}
.university, .school {
  font-family: "Lato", arial, sans-serif;
  font-size: 13px;
  color: #999;
  font-weight: 600;
  padding-left: 30px;
}
.year {
  font-weight: 300;
}
.level {
  font-size: 16px;
  color: #778492;
  font-weight: 400;
}
.level .fa {
  color: #66cb8c;
  display: inline-block;
  padding-right: 2px;
}
.description {
  font-family: 'Lato', arial, sans-serif;
  color: #434343;
  font-size: 14px;
}
.project-img {
  padding: 15px 15px 15px 15px;
  background-color: #f5f5f5;
}
.project-img img {
  margin-left: 15px;
}
.project-img a {
  padding: 10px 0 10px 0;
}
.subtitle {
  font-family: "Lato", arial, sans-serif;
  font-size: 13px;
  color: #999;
  font-weight: 600;
}
.media-body .title {
  margin-top: 20px;
}
.section .section-inner .project-img {
  margin-bottom: 20px;
}
.section .section-inner .project-img:last-child {
  margin-bottom: 0;
}
.section .section-inner .ribbon {
  position: relative;
  overflow: hidden;
  right: -24px;
  bottom: 25px;
  width: 110px; 
  height: 110px;
}
.section .section-inner .ribbon .text {
  font-family: 'Montserrat', sans-serif;
  position: absolute;
  left: 7px;
  top: 14px;
  width: 136px;
  padding: 10px 10px;
  font-size: 15px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #479FC8;
  -webkit-transform: rotate(45deg) translate3d(0, 0, 0);
  -moz-transform: rotate(45deg) translate3d(0, 0, 0);
  -ms-transform: rotate(45deg) translate3d(0, 0, 0);
  -o-transform: rotate(45deg) translate3d(0, 0, 0);
}
.section .section-inner .text:before,
.section .section-inner .text:after {
  content: '';
  position: absolute;
  bottom: -5px;
  border-top: 5px solid #276582;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
}
.section .section-inner .ribbon .text:before {
  left: 0;
}
.section .section-inner .ribbon .text:after {
  right: 0;
}